.grid {
  display: grid;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  gap: 1rem;
  padding: 0 1rem; }

@media (max-width: 768px) {
  .grid {
    grid-template-columns: repeat(2, minmax(0, 1fr)); } }

* {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0;
  box-sizing: border-box;
  color: #f5f5f5; }

body {
  background-color: #1a1a1d; }

body,
html,
#root {
  height: 100%; }

.section {
  width: 100%;
  height: 100%;
  grid-column: 1/13; }
  .section__title {
    font-size: 6rem;
    font-weight: 900;
    grid-column: 1/13; }

.btn {
  width: 10rem;
  height: 4rem;
  outline: none;
  background: linear-gradient(135deg, #c3073f 10.12%, #950740 100%);
  border: none;
  font-weight: 900;
  letter-spacing: 2px;
  transition: 0.8s all; }
  .btn:hover {
    transform: scale(0.8);
    cursor: pointer; }

.topbar {
  padding: 1rem 0;
  z-index: 99; }
  .topbar__logo {
    width: 3rem;
    height: 3rem;
    grid-column: 1 / span 2; }
  .topbar__toggler {
    width: 3rem;
    height: 3rem;
    justify-self: flex-end;
    align-self: center;
    position: relative;
    grid-column: 11 / span 2;
    z-index: 999; }
    .topbar__toggler:hover {
      cursor: pointer; }
    .topbar__toggler:before, .topbar__toggler:after {
      position: absolute;
      right: -2rem;
      width: 2rem;
      height: 2px;
      background-color: #f5f5f5;
      transform: translateX(-100%);
      transition: 0.8s all; }
    .topbar__toggler:before {
      content: "";
      top: 33%; }
    .topbar__toggler:after {
      content: "";
      top: 66%; }
  .topbar input {
    display: none; }
  input:checked + .topbar__toggler:before {
    top: 50%;
    transform: translateX(-100%) rotateZ(-45deg); }
  input:checked + .topbar__toggler:after {
    top: 50%;
    transform: translateX(-100%) rotateZ(45deg); }

.home {
  position: relative;
  top: calc(50% - 4rem);
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row; }
  .home__button {
    margin-top: 1rem; }
    .home__button:not(:last-child) {
      margin-right: 1rem; }
  .home__text {
    font-size: 9rem;
    display: block;
    font-weight: 700; }
    .home__text--white {
      color: #f5f5f5; }
    .home__text--primary {
      color: #c3073f; }
    .home__text--subtitle {
      font-size: 1.5rem;
      color: #f5f5f5; }
  @media (max-width: 768px) {
    .home__text {
      font-size: 3rem; }
      .home__text--subtitle {
        font-size: 1rem; } }

.about {
  grid-column: 1/13; }
  .about__information {
    margin-top: 3rem;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 1rem; }
    .about__information__text {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-direction: row;
      height: 5.6rem;
      padding: 0 1rem;
      font-weight: 400; }
      .about__information__text--left {
        border-left: 1px solid #f5f5f5;
        grid-column: 1 / 2; }
      .about__information__text--right {
        border-right: 1px solid #f5f5f5;
        grid-column: 2/3; }
      .about__information__text--blank {
        border: none; }
      @media (max-width: 768px) {
        .about__information__text {
          grid-column: 1 / span 2;
          margin-bottom: 2rem; }
          .about__information__text--blank {
            display: none; } }

.skill-list {
  margin-top: 2rem;
  grid-column: 1/13; }

.skill-card {
  grid-column: auto / span 3;
  height: 9rem;
  background-color: #1a1a1d;
  border-radius: 10px;
  box-shadow: 0 5px 6px 1px rgba(0, 0, 0, 0.37);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row; }
  .skill-card__logo {
    border: 1px solid #c3073f;
    border-radius: 50%;
    width: 4.5rem;
    height: 4.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row; }
    .skill-card__logo__icon {
      height: 2rem; }
      .skill-card__logo__icon path {
        fill: #c3073f; }
  .skill-card__info {
    margin-left: 1rem; }
    .skill-card__info__name {
      font-weight: 900; }
    .skill-card__info__points {
      margin-top: 0.5rem;
      width: 6.5rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: row; }
    .skill-card__info__point {
      width: 1rem;
      height: 1rem;
      border: 1px solid #f5f5f5;
      border-radius: 50%; }
      .skill-card__info__point--filled {
        background-color: #f5f5f5; }
  @media (max-width: 768px) {
    .skill-card {
      grid-column: auto / span 2; } }

.text-filter {
  border: 1px solid #950740;
  border-radius: 4px;
  padding: 0.5rem;
  grid-column: 1 / span 3;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row; }
  .text-filter__icon {
    height: 1.5rem; }
    .text-filter__icon path {
      fill: #950740; }
  .text-filter__input {
    background-color: transparent;
    height: 100%;
    border: none;
    width: 100%;
    outline: none;
    margin-left: 0.5rem; }

.contact {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row; }
  .contact__option {
    margin-bottom: 2rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row; }
    .contact__option__icon {
      height: 4rem; }
      .contact__option__icon path {
        fill: #c3073f; }
    .contact__option__text {
      font-size: 2rem;
      font-weight: 700;
      letter-spacing: 2px;
      color: #708090;
      margin-left: 1rem;
      text-decoration: none;
      transition: 0.8s all; }
      .contact__option__text:hover {
        color: #4e4e50; }
    @media (max-width: 768px) {
      .contact__option__icon {
        height: 2rem; }
      .contact__option__text {
        font-size: 1rem; } }

.navigation {
  position: absolute;
  top: -150;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #c3073f 10.12%, #950740 100%);
  z-index: 100;
  transition: all 1s; }
  .navigation__links {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    list-style-type: none; }
  .navigation__link {
    position: relative;
    margin-bottom: 1rem;
    font-size: 6rem;
    font-weight: 900;
    letter-spacing: -3px;
    z-index: 999; }
    .navigation__link__ref {
      text-decoration: none; }
    .navigation__link:before {
      content: "";
      width: 0;
      height: 100%;
      background-color: #1a1a1d;
      position: absolute;
      top: 0%;
      left: -5%;
      z-index: -1;
      transition: 1s all; }
    .navigation__link:hover {
      cursor: pointer;
      color: #c3073f; }
      .navigation__link:hover:before {
        width: 110%; }
  .navigation__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0.3;
    width: 5rem;
    z-index: -2; }
  @media (max-width: 768px) {
    .navigation__link {
      font-size: 3.5rem; }
    .navigation__logo {
      top: 85%;
      transform: translateZ(-80%); } }
